.countdown-container {
  font-family: 'repet', sans-serif;
  background-color: black;
  color: pink;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 8%;
  cursor: url('./img/cursor.png'), auto;
}

.countdown-text {
  font-size: 22rem;
  line-height: 100%;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: 0;
}

h1{
  font-size: 4rem;
}
h2{
  font-size: 2rem;
}
.textbox{
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: 'repet';
  src: url('./fonts/repet.ttf');
}
